import { standarFlow } from './../../../../../components/approval/standar/standar-approval.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { DirektoriPerjalananDinasAnggotaService } from 'src/app/sdk/hr/services/direktori-perjalanan-dinas-anggota.service';
import { Component, OnInit } from '@angular/core';
import { Perjalanan_dinas, Perjalanan_dinas_anggota, Pegawai_master } from 'src/app/sdk/hr/models';
import { DirektoriPerjalananDinasService, DirektoriPegawaiMasterService, DirektoriPerjalananDinasRonService } from 'src/app/sdk/hr/services';
import { ActivatedRoute, Router } from '@angular/router';
import { StandarApprovalService } from 'src/app/components/approval/standar/standar-approval.service';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private direktoriPegawaiMasterService : DirektoriPegawaiMasterService,
        private direktoriPerjalananDinasService: DirektoriPerjalananDinasService,
        private direktoriPerjalananDinasRonService: DirektoriPerjalananDinasRonService,
        private routerActive: ActivatedRoute,
        private router: Router,
        private spinner: NgxSpinnerService,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService,
    ) {}

    id_perjalanan_dinas: string = ''
    dataPerjalanan: Perjalanan_dinas = {}
    dataPegawai: Pegawai_master= {}

    ngOnInit() {
        this.routerActive.paramMap.subscribe(
            params => {
                if(params.get('id_perjalanan_dinas'))
                {
                    this.id_perjalanan_dinas = params.get('id_perjalanan_dinas')
                }

                this.getDetailPerjalanan()
                this.getLog()
                this.getRon()

            }
        )


        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailPerjalanan()
                    this.getLog()
                }
            }
        )

    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getDetailPerjalanan()
    {
        this.direktoriPerjalananDinasService.getDirektoriPerjalananDinasId(this.id_perjalanan_dinas).subscribe(
            data => {
                this.dataPerjalanan = data.data;
                this.getPegawai(data.data.id_pegawai)

                this.params = {
                    id: data.data.id_perjalanan_dinas,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_perjalanan_dinas"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )
    }

    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMastersId(id_pegawai).subscribe(
            data=> {
                this.dataPegawai= data.data
            }
        )
    }

    listPerjalananRon: any[] = []
    getRon() {
        let filter = {
            id_perjalanan_dinas: this.id_perjalanan_dinas,
        }
        this.direktoriPerjalananDinasRonService.getDirektoriPerjalananDinasRons(JSON.stringify(filter)).subscribe(
            res => {
                this.listPerjalananRon = res.data
            })
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_perjalanan_dinas })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }


    goToBack() {

        this.router.navigate(['/hr/kalender/perjalanan-dinas/list'])
    }

}
